/* Header */
.header-sticky .header-style-1 {  
    @include fixed(0, n, n, n);   
    width: 100%; 
    z-index: layer(header-fixed); 
    animation: animation-header-sticky 1s;
    box-shadow: 0 0 6px rgba(32, 33, 36, 0.15);
}    
.header-container {
    position: relative; 
    z-index: 1;
    .number {
        @include ab(-5px, n, n, -10px); 
        width: 14px;
        height: 14px;
        font-size: 8px;
        line-height: 9.48px; 
        font-weight: var(--font-body-weight); 
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: var(--main-color);
        @media (min-width: 992px) {
            top: 0;
            width: 20px;
            height: 20px;
            font-size: 12px;
            line-height: 18px; 
        }
    } 
}
.header-desk { 
    background-color: var(--header-bg-desk);
} 
// .header-style-1 { 
    .header-top {
        padding: 24px 0; 
        .header-logo {
            @include margin-end(15px);
            @media (min-width: 1441px) {
                @include margin-end(30px);
            }
            >a {
                display: flex;
            }
        } 
    }
    .header-action-list { 
        @include margin-start(15px);
        @media (min-width: 1441px) {
            @include margin-start(30px);
        }
        .header-action-item {
            &:not(:last-child) {
                position: relative;
                @media (min-width: 992px) {
                    @include padding-end(15px);
                    @include margin-end(15px); 
                }
                @media (min-width: 1200px) {
                    @include padding-end(27px);
                    @include margin-end(27px); 
                }
                &:after {
                    @media (min-width: 992px) {
                        content: "";
                        @include ab(50%, n, n, 0);
                        height: 20px;
                        width: 1px;
                        background-color: #302f2f;
                        transform: translate(0, -50%);
                    }
                }
            }
        }
        .header-account {
            position: relative;
            &:hover {
                @media (hover: hover) {
                    .header-action-item_dropdown {
                        display: block;
                    }
                }
            }
        }
        .header-action-item_dropdown {
            @include ab(100%, 0, n, n);
            background-color: var(--second-bg-color);
            padding: 10px 0;
            border-radius: var(--input-radius);
            z-index: layer(dropdown-account);
            animation: mmfadeIn 0.35s ease-in-out;
            display: none;
            width: 100%;
            li {
                @include list-style-reset;
                padding: 5px 15px;
            }
        }
        .header-account_title {
            align-items: center;
            >svg {
                @include margin-start(10px);
            }
        }
        .header-account_label {
            max-width: 106px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
        }
        .header-cart_title {
            text-transform: uppercase;
        }
        .header-cart_icon {
            @include margin-end(8px);
            align-items: center;
            display: flex;
            svg {
                width: 24px;
                height: 24px;
            }
        }
        .header-cart .header-cart_icon {
            @include margin-end(15px);
        } 
    }  
    .header-bottom {
        position: relative; 
        z-index: 1; 
    }
    .header-bottom_wrapper {
        position: relative;
    }
// }
.header-bottom_right {
    @include margin-start(15px);
    @media (min-width: 1441px) {
        @include margin-start(30px);
    }
    .header-action-item {
        padding-top: 15px;
        padding-bottom: 14px;
        &:not(:last-child) {
            @include margin-end(20px);
            @media (min-width: 1441px) {
                @include margin-end(40px);
            }
        }
    }
    .header-action-item_icon {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        @include margin-end(8px);
    }
    .header-action-item_title {
        font-size: calc(var(--body-font-size) - 2px);
        text-transform: uppercase;
    }
    .header-action-item_label {
        font-weight: var(--font-bold);
        max-width: 106px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }
}
.header-menu {
    height: 100%;
    >.menu-list {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        height: 100%;
        li {
            @include list-style-reset;
        }
        >li { 
            height: 100%;
            padding: 15px 14px;
            display: flex;
            align-items: center; 
            position: relative;
            &:first-child {
                @include padding-start(0); 
            }  
            &:last-child {
                @include padding-end(0); 
            } 
            > .menu-item_wrapper {
                display: flex;
                height: 100%;
                align-items: center;
                position: relative;
                &:before {
                    content: "";
                    @include ab(n, n, -15px, -5px);
                    border-bottom: 10px solid var(--second-bg-color);
                    border-left: 10px solid transparent;
                    border-right: 10px solid transparent;
                    animation: mmfadeIn .35s ease-in-out;
                    display: none;
                }
            } 
            >a, > .menu-item_wrapper >a {
                text-transform: uppercase;
                font-weight: var(--font-bold);
                font-size: var(--body-font-size);
                color: inherit;
            }
            svg {
                @include margin-start(10px);
                @include transition;
                color: inherit;
            }
            .dropdown-menu {
                @include ab(100%, 0, n, n);
                background: var(--second-bg-color);
                padding: 10px 0;
                width: 250px;
                z-index: layer(dropdown-menu);
                animation: mmfadeIn 0.35s ease-in-out;
                border-radius: 0 0 var(--btn1-radius) var(--btn1-radius);
                box-shadow: 0 12px 20px rgba(0, 0, 0, 0.12);
                display: none;
                li {
                    padding: 3px 20px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    > a, >svg {
                        color: var(--body-color-2);
                    }
                    &:hover {
                        @media (min-width: 1025px) {
                            >a, > svg {
                                color: var(--body-link-hover);
                            }
                            >.dropdown-menu--child-sublinks {
                                display: block;
                            }
                        }
                    }
                }
                &.dropdown-menu--child-sublinks {
                    top: 0;
                    left: 100%;
                }
                &.dropdown-menu--mega {
                    width: 100%;
                    padding: 40px 20px;
                    max-height: calc(100vh - var(--header-height) -  20px); 
                    overflow-y: auto;
                    overflow-x: hidden; 
                    @include custom-scrollbar;
                    .dropdown-menu_wrapper {
                        display: flex;
                    }
                    li {
                        display: block;
                    }
                    .menu-item_wrap {
                        padding-bottom: 6px;
                        a, svg {
                            color: var(--body-link);
                            font-weight: var(--font-bold);
                        }
                        &:hover {
                            a, svg {
                                color: var(--body-link-hover);
                            }
                        }
                    }
                    .menu-list--lv1 > li {
                        padding: 0 20px;
                        &:not(:last-child) {
                            padding-bottom: 27px;
                        }
                    }
                    .menu-list--lv2 li {
                        padding: 3px 0;
                    } 
                    .widget-mega-banner {
                        .banner-item {
                            padding: 0 20px;
                            &:not(:last-child) {
                                padding-bottom: 30px;
                            }
                        }
                        .banner-item_image {
                            position: relative;
                            .banner-item_label {
                                @include ab(n, 50%, 20px, n);
                                padding: 10px 15px;
                                margin: 0;
                                width: fit-content;
                                transform: translate(-50%, 0);
                            }
                        }
                    }
                }
            }
            &:hover {
                @media (hover: hover) {
                    >.menu-item_wrapper:before {
                        display: block; 
                    }
                    >.dropdown-menu {
                        display: block;
                    }
                }
            }
        }
    } 
}
.header-search {
    width: 100%;
    flex: 1; 
    position: relative; 
    max-width: 100%;
    @media (min-width: 992px) {
        max-width: 736px;
        margin: 0 auto;
    }
    search-form {
        .search-results_list {
            display: none; 
        }
        &[data-status="loading"] {
            .search-results .search-results_loading {
                display: block; 
            } 
            .search-results_empty, .search-results_list {
                display: none; 
            } 
        }
        &[data-status="show"] {
            .search-results_list {
                display: flex;  
            }
            .search-results_loading, .search-results_empty {
                display: none;
            }
        }
        &.is-visible .search-results {
            display: block;
        }  
    }
    form {
        width: 100%; 
        display: flex;
        .select-group {
            max-width: 130px;
            width: 100%;
            @media (min-width: 1200px) {
                max-width: 155px;
            }
            &:after {
                content: none;
            }
        }
        select {
            border-radius: var(--input-radius) 0 0 var(--input-radius);
            background: var(--second-bg-color);  
            @include border-inline-end(0px, solid, transparent);
            display: block;
            @include padding-end(var(--gutter-lg));
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            min-height: 40px;
            height: auto;
            width: 100%; 
        }
        input { 
            flex: 1;  
            background: var(--second-bg-color);  
            border-radius: var(--input-radius) 0 0 var(--input-radius);
            @include border-inline-end(0px, solid, transparent);
            @media (min-width: 992px) {
                border-radius: 0;
                height: auto;
            }
        }
        .btn {
            flex: 0 0 50px;
            width: 50px;
            height: auto;  
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 0;
            border-radius: 0 var(--input-radius) var(--input-radius) 0; 
            background-color: var(--btn2-bg-cl);
            color: var(--btn2-cl);
            @media (min-width: 992px) {
                background-color: var(--btn1-bg-cl);
                color: var(--btn1-cl);
            }
            svg {
                width: 20px;
                height: 20px; 
                color: var(--btn2-cl);
                @media (min-width: 992px) {
                    color: var(--btn1-cl);
                }
                path {
                    fill: currentColor;
                }
            }
            .svg-loading {
                display: none;
                color: var(--btn2-cl);
                @media (min-width: 992px) {
                    color: var(--btn1-cl);
                }
            }
            &.pending {
                .svg-search {
                    display: none;
                }
                .svg-loading {
                    display: block;
                }
            }
        }
    }
    .search-results {
        @include ab(calc(100% + 7px), 0, n, 50px); 
        background: var(--second-bg-color); 
        z-index: layer(search-result); 
        padding: 0; 
        display: none;
        border-radius: var(--input-radius);
        animation: mmfadeIn 0.35s linear; 
        box-shadow: 0px 5px 24px rgba(35, 35, 35, 0.18); 
        .search-results_list {
            max-height: 362px;
            overflow: hidden;
            flex-direction: column;
        } 
        .search-results_loading {
            display: none; 
        }
        .search-results_empty {
            padding: var(--gutter-sm);
        }
        button {
            width: 100%;
            border-radius: 0 0 var(--input-radius) var(--input-radius);
            margin: auto 0 0;
        }
        .search-resutls_group_title {
            font-size: calc(var(--body-font-size) + 2px);
            font-weight: var(--font-bold);
            @include font-heading;
            flex: 0 0 100%;
            padding: 0 var(--gutter-xs) var(--gutter-xs);
        }
        .search-resutls_group {
            margin-bottom: 5px;
            &.search-resutls_group--products {
                display: flex;
                flex-wrap: wrap;
                .search-resutls_item {
                    flex: 0 0 100%;
                    width: 100%;
                    @media (min-width: 1200px) {
                        width: calc(50% - 30px);
                        flex: 0 0 calc(50% - 30px);
                    }
                }
            }
        }
        .search-results_list-inner {
            height: calc(100% - 58px);
            overflow-x: hidden;
            overflow-y: auto;
            padding-top: var(--gutter-sm);
            @include custom-scrollbar; 
            .search-resutls_item:nth-child(n) {   
                animation-delay: calc((var(--counter) * 0.1) + 0.1s);     
            } 
        }
        .search-resutls_item {
            display: flex;
            align-items: center; 
            animation: animation-top-bottom .5s both;
            padding: 0 var(--gutter-xs) 16px;
            font-size: var(--body-font-size); 
            font-weight: var(--font-body-weight); 
            line-height: 1.4; 
            .search-resutls_item-image {
                flex: 0 0 76px;
                @include margin-end(8px);
            }
            .search-resutls_item-info {
                flex: 1;
            } 
            .search-resutls_item-title {
                margin-bottom: var(--gutter-xxs); 
            }
            .search-resutls_item-price {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                font-weight: var(--font-bold);
                color: var(--price-color);
                font-size: var(--product-card-price-fs);
                .price-sale {
                    font-size: var(--product-card-price-sale-fs);
                    color: var(--price-sale-color);
                    @include margin-end(12px); 
                } 
                .price-compare { 
                    font-size: var(--product-card-price-compare-fs); 
                    color: var(--price-compare-color);
                    font-weight: var(--font-body-weight);  
                }
            } 
        }
    }
}
.header-cart {
    .header-cart_icon {
        position: relative;
        z-index: layer(icon-cart);
        &:after {
            content: "";
            @include ab(100%, 50%, n, n);
            border-bottom: 10px solid var(--second-bg-color);
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            animation: mmfadeIn .35s ease-in-out;
            transform: translate(-50%, 0);
            display: none;
            margin-top: 3px;
            z-index: layer(dropdown-cart);
            @media (min-width: 992px) {
                margin-top: 0;
            }
        }
    }
    .dropdown-cart {
        background: var(--second-bg-color);
        border-radius: 4px;
        width: calc(100% - 30px);
        z-index: layer(dropdown-cart);
        @include ab(100%, n, n, 15px);
        animation: mmfadeIn .35s ease-in-out;
        box-shadow: 0 6px 16px rgba(27, 24, 24, 0.24);
        display: none;
        @media (min-width: 376px) {
            width: 330px;
        }
        @media (min-width: 750px) {
            right: 32px;
        }
        @media (min-width: 992px) {
            margin-top: 10px;
            right: 0;
        }
        &[data-cart-item-count="0"] {
            .cart-form {
                display: none;
            }
            .cart-empty {
                display: block;
            }
        }
    }
    .cart-empty {
        display: none;
        padding: 15px;
    }
    .dropdown-cart_title {
        border-bottom: 1px solid var(--body-border-color);
        padding: 7px 15px;
        font-size: calc(var(--body-font-size) + 2px);
        font-weight: var(--font-bold);
        .btn-close {
            display: flex;
            font-family: inherit;
            font-weight: inherit;
            font-size: inherit;
        }
        .svg-close {
            width: 14px;
            height: 14px;
            color: var(--body-color);
            path {
                fill: currentColor;
            }
        }
    }
    .cart-item-list {
        max-height: 50vh;
        overflow-y: auto;
        overflow-x: hidden;
        @include custom-scrollbar;
    }
    .cart-line-item {
        display: flex;
        flex-wrap: wrap;
        padding: 15px 15px 10px;
        border-bottom: 1px solid var(--body-border-color);
        .cart-line-item_info {
            flex: 0 0 95px;
            width: 95px;
            @include margin-end(12px);
            .cart-line-item_img {
                width: 100%;
                height: 100%;
            }
        }
        .cart-line-item_info_content {
            flex: 1;
        }
        .cart-line-item_title {
            overflow: hidden;
            white-space: initial;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            word-wrap: break-word;
            text-overflow: -o-ellipsis-lastline;
            font-size: var(--body-font-size);
            margin-bottom: 8px;
            font-style: var(--font-heading-style);
            @include font-heading;
        }
        .cart-line-item_wrapper {
            display: flex;
            color: var(--body-color-2);
            font-size: calc(var(--body-font-size) - 2px);
            .cart-line-item_qty {
                @include margin-end(30px);
            }
        }
        .btn-remove {
            flex: 0 0 100%;
            margin-top: 6px;
            color: #086479;
            text-decoration: underline;
            text-transform: none;
            text-align: start;
            padding: 0;
            font-size: var(--body-font-size);
        }
    }
    .dropdown-cart_footer {
        padding: 12px 15px 15px;
        .cart-total_label {
            color: var(--body-color-2);
        }
        .cart-total_price {
            font-size: calc(var(--body-font-size) + 2px);
            font-weight: var(--font-bold);
        }
        .btn {
            width: calc(50% - 7px);
            margin-top: 25px;
            padding: 9px 10px;
            font-size: var(--body-font-size);
        }
        .btn-review-cart {
            border: 1px solid var(--body-border-color);
            border-radius: var(--btn1-radius);
        }
    }
    &.active {
        .dropdown-cart {
            display: block;
        }
        .header-cart_icon:after {
            display: block;
        }
    } 
}
/* Header mobile */
.header-mobile {
    .header-mobile_top {
        background-color: #1e1e1e;
        display: flex;
        padding: 13px 15px;
        position: relative;
        @media (min-width: 750px) {
            padding-left: 32px;
            padding-right: 32px;
        }
        .header-hamburger {
            color: #ffffff;
            margin: 0;
            @include margin-end(13px);
            display: flex;
            align-items: center;
            cursor: pointer;
        }
        .header-logo {
            display: flex;
            >a {
                display: flex;
                align-items: center;
            }
        }
        .header-icons {
            @include margin-start(auto);
            .icon-list {
                display: flex;
                height: 100%;
                > .icon-item {
                    height: 100%;
                    display: flex;
                    align-items: center;
                    @include margin-start(20px);
                    > a {
                        color: #ffffff;
                        display: flex;
                        align-items: center;
                    }
                    &.header-cart a {
                        @include margin-end(10px);
                    }
                }
            }
        }
    }
    .header-mobile_bottom {
        padding: 12px 15px;
        background-color: var(--main-color);
        @media (min-width: 750px) {
            padding-left: 32px;
            padding-right: 32px;
        }
    }
}
html.no-js #mobile-drawer-input:checked ~ #header-mobile-drawer-wrapper .header-mobile_drawer,
#mobile-drawer-input:checked ~ .header-mobile_drawer {
    .drawer-overlay {
        @include black-bg-open;
    }
    >.drawer-wrapper {
        @include visibility-visible;
        animation: animation-menu-right-to-left 0.5s ease-in-out;
        >ul >li {
            input:checked ~ .drawer-sub-menu {
                transform: translateX(0);
                @include visibility-visible;
                pointer-events: initial;
            }
        }
    }
    .drawer-close {
        @include visibility-visible;
        animation: animation-menu-right-to-left 0.5s ease-in-out;
    }
}
.header-mobile_drawer {
    .drawer-overlay {
        @include fixed(0, 0, 0, 0);
        @include black-bg;
    }
    .drawer-close {
        @include fixed(0, 85%, n, n);
        background-color: #302f2f;
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 48px;
        height: 48px;
        z-index: layer(popup-content);
        animation: animation-menu-left-to-right 0.35s ease-in-out;
        @include visibility-hidden;
        @include transition;
        @include point-cursor;
        @media (min-width: 375px) {
            left: 315px;
        }
        svg {
            color: #ffffff;
        }
    }
    >.drawer-wrapper {
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        width: 85%;
        z-index: layer(popup-content);
        background: var(--second-bg-color);
        overflow: hidden;
        @include visibility-hidden;
        animation: animation-menu-left-to-right 0.35s ease-in-out;
        @include transition;
        @media (min-width: 375px) {
            width: 315px;
        }
        > ul {
            overflow-y: auto;
            overflow-x: hidden;
            height: 100%;
            padding: 15px;
            @include custom-scrollbar;
            li {
                @include list-style-reset;
                a, svg {
                    color: var(--body-link);
                }
            }
            >li {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 14px 0;
                >a, > svg {
                    text-transform: uppercase;
                    font-weight: var(--font-bold);
                }
                >label {
                    @include point-cursor;
                    margin: 0;
                    @include margin-start(10px);
                } 
                .btn-back {
                    display: flex;
                    align-items: center;
                    padding: 5px 15px 14px;
                    margin: 0;
                    color: var(--body-link);
                    border-bottom: 1px solid var(--body-border-color);
                    @include point-cursor; 
                    svg {
                        @include margin-end(10px);
                        color: inherit;
                        path {
                            fill: currentColor;
                        }
                    }
                }
                >.drawer-sub-menu {
                    @include ab(0, 0, n, n);
                    padding: 15px 0;
                    width: 100%;
                    height: 100%;
                    z-index: layer(popup-content);
                    background: var(--second-bg-color);
                    overflow-y: auto;
                    overflow-x: hidden;
                    @include point-event;
                    @include visibility-hidden;
                    transform: translate(375px, 0);
                    @include transition;
                    @include custom-scrollbar;
                    .banner-item {
                        padding: 8px 15px;
                    }
                    .menu-list--lv1 > li {
                        padding: 14px 15px;
                    }
                    .menu-item_wrap {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        font-weight: var(--font-bold);
                        color: var(--body-link);
                        a {
                            color: inherit;
                        }
                        label {
                            width: 20px;
                            text-align: center;
                            @include point-cursor;
                            margin: 0;
                            @include margin-start(10px);
                            transition: transform 0.35s ease-in-out;
                            svg {
                                color: inherit;
                            }
                            &.active {
                                transform: rotate(180deg);
                            }
                        }
                    }
                    input:checked ~ ul.collapse-menu {
                        display: block;
                    }
                    ul {
                        &.collapse-menu {
                            padding: 8px 0 0; 
                            display: none;
                            li {
                                padding: 8px 0;
                            }
                        }
                        li  a {
                            color: var(--body-color-2);
                        }
                    }
                }
                &.menu-item--help {
                    display: block;
                    margin-top: 10px;
                    padding-top: 25px;
                    border-top: 1px solid var(--body-border-color);
                    .menu-item_wrapper:not(:last-child) {
                        margin-bottom: 10px;
                    }
                    .menu-item--help_title {
                        font-size: calc(var(--body-font-size) - 2px);
                        margin-bottom: 10px;
                        color: var(--body-link);
                    }
                    .menu-item_icon {
                        display: flex;
                        justify-content: center;
                        @include margin-end(10px);
                    }
                    .menu-item_content {
                        color: var(--body-link);
                        a {
                            color: inherit;
                        }
                        p {
                            margin: 0;
                            color: inherit;
                        }
                    }
                }
            }
        }
    }
}
/* End header mobile */
