/* Footer */
footer {
    margin-top: auto;          //fix short content templateCaptcha
}
.section-footer-widget {
    .block-wrapper {
        >div {
            margin-bottom: 20px;
            align-items: flex-start;
            &.widget-logo {
                margin-bottom: 25px; 
            }
        }
        .widget-payment {
            margin: 30px 0 0;
            .block-widget_title {
                margin-bottom: 12px; 
            }
            ul li:not(:last-child) {
                @include margin-end(10px);
            } 
        } 
    }
    .block-widget_title {
        font-size: var(--body-font-size); 
        color: #ffffff;
        font-weight: var(--font-body-weight);
        margin-bottom: 19px;
    }
    .block-information {
        .text-1, .text-2 {
            color: #ffffff;
        } 
        .icon {
            display: flex;
            align-items: center;
            @include margin-end(6px);
            svg {
                color: #ffffff;
            }
        } 
    }
    .block-menu {
        .block-wrapper {
            @media (min-width: 1000px) {
                padding-top: 14px;  
            }
        } 
        .menu-list li {
            @include list-style-reset;
            margin-bottom: 11px;  
            a {
                color: var(--text-miscellaneous);
            }
        }
    }
    .block-newsletter {
        .block-wrapper {
            @media (min-width: 1000px) {
                padding-top: 14px;  
            }
        } 
        .subtext {
            color: var(--text-miscellaneous); 
        }
        form {
            width: 100%;
            display: flex;
            height: 40px;
            input {
                flex: 1;
                border-width: 1px; 
                border-style: solid;
                border-color: #302f2f;
                background-color: transparent; 
                color: var(--text-miscellaneous); 
                height: 100%;
                border-radius: var(--input-radius) 0 0 var(--input-radius);
            }
            .btn {
                width: 60px;  
                height: 100%;
                padding: 0 5px;  
                border-radius: 0 var(--input-radius) var(--input-radius) 0;
                .btn-text {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
    }
    .widget-social {
        .title {
            color: #ffffff; 
            margin-bottom: 11px;
        }
        ul li {
            a {
                width: 30px;
                height: 30px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-width: 1px;
                border-style: solid;
            } 
            &:not(:last-child) {
                @include margin-end(8px);
            }
        }
    }
}
.footer .section-footer {
    border-top: 1px solid #212121;
    .section-wrapper {
        flex-wrap: wrap;
    }
    .copyright {
        font-size: calc(var(--body-font-size) - 2px);
        order: 2;
        flex: 0 0 100%;
        padding-top: 12px;
        @media (min-width: 750px) {
            padding-top: 0;
            order: 1; 
            flex: auto;
        }
    }
    p {
        margin: 0; 
    }
    a {
        color: inherit;
    }
    .multiple-currencies {
        border: 1px solid #302f2f;
        border-radius: var(--input-radius); 
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        max-width: 100px;
        width: 100%;
        order: 1;
        flex: 0 0 100%;
        @media (min-width: 750px) {
            order: 2;
            flex: 1; 
        }
        select {
            border: none;
            background: transparent;
            box-shadow: none;
            color: inherit;
            width: 100%;  
        }
        svg {
            @include ab(50%, n, n, 8px);
            pointer-events: none;
            transform: translate(0, -50%); 
        }
    }
}
/* End footer */