/* Product item */
.section-featured-collection, 
.secion-related-products, 
.section-featured-blog {
	@media (max-width: 991px) {
		overflow: hidden; 
	}
	.slider-container {
		.tns-outer {
			margin: -10px;  
			@media (min-width: 992px) {
				margin: -20px;
			}
		}
		.tns-ovh {
			padding: 10px;
			@media (min-width: 992px) {
				padding: 20px;
			}
		}
	} 
} 
product-card {
	min-width: 0;
}
.product-card {
	background-color: var(--second-bg-color);
	padding: var(--gutter-xs);
	height: 100%;
	display: flex;
	flex-direction: column;
	@include transition;
	@media (min-width: 1200px) {
		padding: var(--gutter-sm);
	}
	.product-card_header {
		position: relative;
		margin-bottom: var(--gutter-sm); 
	}
	.product-card_media {
		position: relative;
		overflow: hidden;
	}
    &.has-hover-img .product-card_header {
        a > .image__style {
            transition: opacity .2s ease-in-out, visibility .2s ease-in-out; 
        } 
        .product-card_media_behind {
            @include ab(0, 0, 0, 0);
            @include visibility-hidden;
            transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
        }
		&:hover {
			@media (hover: hover) {
                .product-card_media a > .image__style {
                    @include visibility-hidden; 
                }
                .product-card_media_behind {
                    @include visibility-visible;
                }
			}
		}
    }
	.produc-vendor { 
		font-size: var(--product-card-vendor-fs);
		color: var(--text-miscellaneous);
		text-transform: uppercase;
		font-weight: var(--font-body-weight); 
		margin-bottom: 2px;
		a {
			color: inherit; 
		}
	}
	.product-card_title {
		font-size: var(--product-card-title-fs);
		font-weight: var(--font-body-weight); 
		line-height: 1.4; 
		margin-bottom: 13px; 
	}
	&.balance-row-1 .product-card_title > a { 
		overflow: hidden;
	    white-space: initial;
	    display: -webkit-box;
	    -webkit-line-clamp: 1; 
	    -webkit-box-orient: vertical;
	    word-wrap: break-word;
        text-overflow: -o-ellipsis-lastline;
	} 
	&.balance-row-2 .product-card_title > a { 
		overflow: hidden;
	    white-space: initial;
	    display: -webkit-box;
	    -webkit-line-clamp: 2;
	    -webkit-box-orient: vertical;
	    word-wrap: break-word;
        text-overflow: -o-ellipsis-lastline;
	} 
	&.balance-row-3 .product-card_title > a { 
		overflow: hidden;
	    white-space: initial;
	    display: -webkit-box;
	    -webkit-line-clamp: 3;
	    -webkit-box-orient: vertical;
	    word-wrap: break-word;
        text-overflow: -o-ellipsis-lastline;
	} 
	.product-card_footer {
		display: flex; 
		align-items: center;
		justify-content: space-between;  
		margin-top: auto;
	}
	.product-card_actions {   
		display: flex;
		justify-content: center;
		width: 100%;
		@include ab(n, 0, 0, 0);
		z-index: layer(button-product);
		@media (min-width: 1025px) {
			justify-content: space-between;
		}
		.product-card_quick-view {
			display: none;
			width: calc(50% - 5px);
			padding: 11px 5px;
			font-size: calc(var(--btn2-fs) - 2px);
			height: 100%;
			@media (min-width: 1025px) {
				display: block;
				transform: translate(0, 50px);
				transition: transform 0.3s ease-in-out;
			}
			@media (max-width: 1400px) and (min-width: 1025px) {
				font-size: calc(var(--btn2-fs) - 4px);
			}
		}
		.product-form {
			width: 100%;
			@media (min-width: 1025px) {
				width: calc(50% - 5px);
				transform: translate(0, 50px);
				transition: transform 0.3s ease-in-out;
				transition-delay: 0.1s;
			}
			.btn-add-to-cart {
				height: 100%;
				width: 100%;
				padding: 11px 5px;
				font-size: calc(var(--btn1-fs) - 2px);
				@media (max-width: 1400px) and (min-width: 1025px) {
					font-size: calc(var(--btn1-fs) - 4px);
				}
			}
		}
	}
	.btn-add-to-cart, .product-card_quick-view {
		position: relative;
		.svg-loading {
			display: none;  
			@include ab(50%, 50%, n, n);
			transform: translate(-50%, -50%);
			color: var(--btn1-hover-cl); 
			circle {
				stroke: currentColor; 
			}
		}
		&.pending {
			@include point-event;
			> span {
				color: transparent;
			}
			.svg-loading {
				display: block;
			}
		}
	}
	&:hover {
		@media (hover: hover) {
			box-shadow: 0px 5px 20px rgba(35, 35, 35, 0.18); 
		}
		@media (min-width: 1025px) {
			.product-card_actions {
				.product-card_quick-view, .product-form {
					transform: translate(0, 0);
				}
			}
		}
	}
}
.proudct-price {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: flex-start;
	font-weight: var(--font-bold);
	color: var(--price-color);
	font-size: var(--product-card-price-fs);
	.price-sale {
		font-size: var(--product-card-price-sale-fs);
		color: var(--price-sale-color);
		@include margin-end(5px); 
		@media (min-width: 750px) {
			@include margin-end(12px);
		}
	} 
	.price-compare {
		font-size: var(--product-card-price-compare-fs); 
		color: var(--price-compare-color);
		font-weight: var(--font-body-weight);  
	}
}
.product-card_labels {
	@include ab(-7px, calc(-1 * calc(var(--gutter-xs) + 5px)), n, n);
	z-index: layer(product-label);
	@media (min-width: 1200px) {
		left: calc(-1 * calc(var(--gutter-sm) + 5px));
	} 
	.label {
		font-size: calc(var(--body-font-size) - 2px);
		line-height: 1.5;
		text-transform: uppercase;
		padding: 1px 8px; 
		text-align: center;
		position: relative;
		color: var(--sale-lb-cl);
		background: var(--sale-lb-bgcl);
		&:after {
			content: "";
			@include ab(n, 0, -4px, n);
			border-bottom: 4px solid transparent;
			@include border-inline-end(5px, solid, var(--sale-lb-bgcl));
		}  
		&:not(:last-child) {
			margin-bottom: 6px; 
		} 
		&.label-hot {
			color: var(--hot-lb-cl);
			background: var(--hot-lb-bgcl);
			&:after {
				@include border-inline-end(5px, solid, var(--hot-lb-bgcl));
			}
		}
		&.label-new {
			color: var(--new-lb-cl);
			background: var(--new-lb-bgcl);  
			&:after {
				@include border-inline-end(5px, solid, var(--new-lb-bgcl));
			}
		}
	}
} 
/* End product item */
/* Product review */
.spr-icon {
    color: var(--rating-star-color);
    @include margin-end(5px);
    &.spr-icon-star-empty {
        color: var(--rating-star-empty-color); 
    }
}
.spr-badge .spr-badge-caption {
    color: var(--text-miscellaneous);
    font-size: calc(var(--body-font-size) - 2px);
} 
.product-reviews {
    .btn-primary {
        @include point-cursor;
        display: block;
		height: auto;
        padding: 14px 15px;  
        text-align: center; 
        box-shadow: none;
        text-decoration: none;
        font-size: var(--btn1-fs);
        font-weight: var(--btn1-fw);
        @include font-body;  
        transition: background 0.25s ease-in-out, color 0.25s ease-in-out, border 0.25s ease-in-out;
        color: var(--btn1-cl);
        background: var(--btn1-bg-cl);
        border: var(--btn1-border-width) solid var(--btn1-border-cl); 
		border-radius: var(--btn1-radius);  
        &:hover {
            @media (hover: hover) {
                text-decoration: none;
                color: var(--btn1-hover-cl);
                background: var(--btn1-hover-bg-cl);
                border-color: var(--btn1-hover-border-cl); 
            }  
        }
    }
}
/* End product review */ 